.side-filters {
    line-height: initial !important;
}


.side-filters hr {
    width: 90%;
    margin: 5px 0 !important;
    margin-top: 0px !important;
}

.side-filters label {
    margin: 1px !important;
    font-size: 14px !important;
    color: #666666;
}

.side-filters span hr {
    margin: 0;
    color: #84C225;
    margin-bottom: 0 !important;
    height: 2px;
    background-color: #84C225;
}

.side-filters span .brand-hr {
    width: 45px;
}
.side-filters span .season-hr {
    width: 75px;
}
.side-filters span .country-hr {
    width: 130px;
}

.side-filters input[type=text] {
    border: 1px solid silver;
    margin-top: 5px;
    width: 90%;
    background-color: #FFFFFF;
    font-size: 13px;
    padding: 3px 5px;
    margin-bottom: 10px;
    color: #58595B;
}

.side-filters input[type=text]:focus {
    outline: 1px solid greenyellow;
}



.side-filters input[type=radio] {
    cursor: pointer;
    border: 1px solid silver;
    -webkit-appearance: none;
     margin-right: 5px;
     width: 15px;
     height: 15px;
     accent-color: green;
}

.side-filters input[type=radio]:checked {
    background: url("data:image/jpeg;base64,") no-repeat center center;
    background-size: 9px 9px;
    background-position: center;
}

.side-filters input[type=radio]:focus {
    outline-color: transparent;
}



/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    .side-filters {
        display: none;
    }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
    .side-filters {
        display: none;
    }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
    .side-filters {
        display: initial;
    }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {}